<template>
  <div id="main">
      <article class="post featured">
        <header class="major">
          <span class="date">Mon compte</span>
          <div id="firebaseui-auth-container"></div>
          <div id="connected" v-bind:class="{ visible: userId !== '' }">Connecté : {{ name }}</div>
          <div id="logout" v-bind:class="{ visible: userId !== '' }"><button @click='logOut'>Déconnexion</button></div>
        </header>
      </article>
  </div>
</template>

<script>
import firebase from 'firebase'
import firebaseui from 'firebaseui'

export default {
  name: 'account',

  data () {
    return {
      photo: '',
      userId: '',
      name: '',
      email: ''
    }
  },

  methods: {
    logOut () {
      firebase.auth().signOut()
      this.name = ''
      this.email = ''
      this.photo = ''
      this.userId = ''
    }
  },

  mounted () {
    console.log('connection mounted')

    // auth
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        console.log('connected as ' + user.displayName)
        this.name = user.displayName
        this.email = user.email
        this.photo = user.photoURL
        this.userId = user.uid
      } else {
        console.log('not connected')
        var uiConfig = {
          signInSuccessUrl: '/',
          signInOptions: [
            firebase.auth.GoogleAuthProvider.PROVIDER_ID,
            firebase.auth.FacebookAuthProvider.PROVIDER_ID,
            firebase.auth.EmailAuthProvider.PROVIDER_ID
          ]
        }
        if (this.$root.ui) {
          this.$root.ui.reset()
        } else {
          this.$root.ui = new firebaseui.auth.AuthUI(firebase.auth())
        }
        this.$root.ui.start('#firebaseui-auth-container', uiConfig)
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#connected { display:none; padding-bottom:3em; }
#logout { display:none; }
#connected.visible { display:inherit; }
#logout.visible { display:inherit; }
</style>



