<template>
  <div id="app">

    <!-- Wrapper -->
      <div id="wrapper" class="fade-in">

        <!-- Intro -->
          <div id="intro">
            <h1 class="fredoka">An@web</h1>
            <p>Portail d'applications web.</p>
            <ul class="actions">
              <li><a href="#header" class="button icon solo fa-arrow-down scrolly">Continue</a></li>
            </ul>
          </div>

        <!-- Header -->
          <header id="header">
            <router-link to="/" class="logo">An@web</router-link>
          </header>

        <!-- Nav -->
          <nav id="nav">
            <ul class="links">
              <li v-bind:class="{ active: this.$route.path == '/' }"><router-link to="/">Home</router-link></li>
              <li v-bind:class="{ active: this.$route.path == '/contact', connexion: this.connexionText != 'Online' }"><router-link to="contact">Contact</router-link></li>
              <li v-bind:class="{ active: this.$route.path == '/info' }"><router-link to="info">Infos</router-link></li>
              <li v-bind:class="{ active: this.$route.path == '/account' }"><router-link to="account">Mon compte</router-link></li>
              <li><a :style="connexionColor">{{ connexionText }}</a></li>
            </ul>
            <ul class="icons" v-bind:class="{ connexion: this.connexionText != 'Online' }">
              <li><a href="mailto:?Subject=An@web.fr&amp;Body=Portail%20d'applications%20:%20https://www.anaweb.fr" class="icon fa-envelope-o"><span class="label">Mail</span></a></li>
              <li><a href="http://www.facebook.com/sharer.php?u=https://www.anaweb.fr/" class="icon fa-facebook"><span class="label">Facebook</span></a></li>
              <li><a href="https://plus.google.com/share?url=https://www.anaweb.fr/" class="icon fa-google"><span class="label">Google+</span></a></li>
              <li><a href="http://www.linkedin.com/shareArticle?mini=true&amp;url=https://www.anaweb.fr/" class="icon fa-linkedin"><span class="label">Linkedin</span></a></li>
              <li><a href="https://twitter.com/share?url=https://www.anaweb.fr/&amp;text=Anaweb.fr,%20Portail%20d'applications" class="icon fa-twitter"><span class="label">Twitter</span></a></li>
            </ul>
          </nav>

        <!-- Main -->
        <router-view></router-view>

        <!-- End section -->
        <!-- <div id="main">
          <section style="text-align:center;">
          </section>
        </div> -->

        <!-- Copyright -->
          <div id="copyright">
            <ul><li>&copy; Anaweb - {{ version }}{{ envLetter }}</li><li>Design: <a href="http://gregory.scheemacker.fr">Grégory Scheemacker</a></li></ul>
          </div>

      </div>
  </div>
</template>

<script>
import store from './store'

export default {
  name: 'app',

  data () {
    return {
      connexionColor: 'color:red',
      connexionText: 'Offline',
      envLetter: process.env.NODE_ENV.charAt(0)
    }
  },

  computed: {
    version () {
      return store.state.currentVersion
    }
  },

  methods: {
    showConnexion (event) {
      console.log('show')
      this.connexionColor = 'color:green'
      this.connexionText = 'Online'
      console.log('show' + this.connexionText)
    },
    hideConnexion (event) {
      console.log('hide')
      this.connexionColor = 'color:red'
      this.connexionText = 'Offline'
      console.log('hide' + this.connexionText)
    },
    initApp () {
      console.log('initApp')
      // get data
      this.$http.get(process.env.APP_CONFIG_JSON).then(response => {
        var dataApp = response.data.anaweb
        if (dataApp !== undefined) {
          store.state.lastVersion = dataApp.version
          store.state.helloTitle = dataApp.helloTitle
          store.state.helloMessage = dataApp.helloMessage
          store.state.applications = response.data.applications
        }
      }, response => {
        // error callback
        console.log('error call data')
      })
    }
  },

  activated () {
    if (!navigator.onLine) this.hideConnexion
    else this.showConnexion
    console.log('activated' + this.connexionText)
  },

  mounted () {
    window.addEventListener('online', this.showConnexion)
    window.addEventListener('offline', this.hideConnexion)
    if (!navigator.onLine) this.hideConnexion()
    else this.showConnexion()
    console.log('mounted' + this.connexionText)
    this.initApp()
  }
}
</script>

<style>
body {
  margin: 0;
}
ul.connexion {
  display: none;
}
#nav ul.links li.connexion {
  display: none;
}
</style>



