<template>
  <div id="main">
    <section style="padding-bottom:1em;">
      <h4>VERSION</h4>
      <div v-if="currentVersion == lastVersion">{{ currentVersion }} (vous êtes à jour)</div>
      <div v-else>Nouvelle version disponible (rafraîchissez votre navigateur)</div>
    </section>
    <section style="padding-bottom:1em;">
      <h4>Web Application</h4>
      <div>Ce site est une application web, vous pouvez l'installer comme une application sur votre mobile.</div>
      <div>
        <a v-bind:href="installImg1"><img v-bind:src="installImg1" class="install"/></a>
        <a v-bind:href="installImg2"><img v-bind:src="installImg2" class="install"/></a>
        <a v-bind:href="installImg3"><img v-bind:src="installImg3" class="install"/></a>
      </div>
    </section>
    <section style="padding-bottom:3em;">
      <h4>Navigateurs & OS</h4>
      <div>Supportés par l'application: Chrome, Firefox, Opera & Android</div>
      <div>Non testés: Edge, Safari & iOS</div>
    </section>
    <section style="padding-bottom:3em;">
      <h4>Mentions légales</h4>
      <div>Ce site est la propriété de <a href="http://gregory.scheemacker.fr">Grégory Scheemacker</a>.</div>
    </section>
  </div>
</template>

<script>
import store from '../store'

export default {
  name: 'info',

  data () {
    return {
      installImg1: process.env.APP_INSTALL_IMG1,
      installImg2: process.env.APP_INSTALL_IMG2,
      installImg3: process.env.APP_INSTALL_IMG3
    }
  },

  computed: {
    currentVersion () {
      return store.state.currentVersion
    },
    lastVersion () {
      return store.state.lastVersion
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}
img.install {
  width:20%
}
</style>



