import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

var store = new Vuex.Store({
  state: {
    currentVersion: 'v1.0.11',
    lastVersion: '',
    helloTitle: 'Welcome !',
    helloMessage: 'An@web',
    applications: [],
    user: null
  },
  getters: {
    applications: state => {
      return state.applications.filter(app => app.active)
    }
  }
})

export default store



