<template>
  <div id="main">
    <!-- Featured Post -->
      <article class="post featured">
        <header class="major">
          <span class="date" id="homeDate">{{ today }}</span>
          <h2>{{ title }}</h2>
          <p v-html="message">{{ message }}</p>
        </header>
      </article>

    <!-- Posts -->
      <section class="posts">
        <article v-for="app in applications" key="app.id">
          <header>
            <h2>{{ app.title }}</h2>
          </header>
          <a class="image fit"><img v-bind:src="app.image" :alt="app.title" /></a>
          <p>{{ app.message }}</p>
        </article>
      </section>
  </div>
</template>

<script>
import store from '../store'

export default {
  name: 'hello',

  data () {
    return {
      today: '',
      photo: '',
      userId: '',
      name: '',
      email: '',
      connectionTitle: 'Connexion'
    }
  },

  computed: {
    title () {
      return store.state.helloTitle
    },
    message () {
      return store.state.helloMessage
    },
    applications () {
      return store.getters.applications
    }
  },

  methods: {
    getMonthStrings () {
      return ['janvier', 'février', 'mars', 'avril', 'mai', 'juin', 'juillet', 'août', 'septembre', 'octobre', 'novembre', 'décembre']
    }
  },

  mounted () {
    console.log('hello mounted')

    // init
    var today = new Date()
    var d = today.getDate()
    var m = this.getMonthStrings()[today.getMonth()]
    var y = today.getFullYear()
    this.today = d + ' ' + m + ' ' + y
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#connected { display:none; }
#logout { display:none; }
#connected.visible { display:inherit; }
#logout.visible { display:inherit; }
</style>



